	import util from '@/libs/util'
	import wxjssdk from '@/libs/wxjssdk.mixin'
	export default{
		mixins: [ wxjssdk ],
		props: {
//			src: {
//				type: String,
//				default: ''
//			},
//			duration: {
//				type: String,
//				default: '0'
//			},
			file: {
				type: Object,
				default: ()=>{
					return {
						file_url: '',
						duration: '0',
						_is_wx_file: false
					}
				}
			},
			active: {
				type: Boolean,
				default: false
			}	//控制播放、暂停
		},
		data () {
			return {
				isPlaying: false,
//				true_duration: Math.ceil(this.file.duration||0),
			}
		},
		watch: {
			active: function (val) {
				if(val){
					this.play()
				}else{
					this.ended()
				}				
			},
//			'file.duration': function (val) {
//				if(val){
//					this.true_duration = Math.ceil(val)
//				}
//			}
		},
		created () {
			if(!this.file.duration||this.file.duration==0){
				this.get_duration()				
			}			
		},
		methods: {
			togglePlay () {
				if(!this.isPlaying){					
					this.play()
				}else{
					this.stop()
				}
			},
			play () {
				this.onVoicePlayEnd(this.file.file_url)
				.then(()=>{
					this.stop()
				})
				
				this.isPlaying = true
				if(this.file._is_wx_file){
					this.playVoice(this.file.file_url)
				}
	            this.$refs.player.play()
	            
		    },
		    stop () {
		    	this.isPlaying = false
		    	if(this.file._is_wx_file){
					this.stopVoice(this.file.file_url)
				}
	            this.$refs.player.pause()
		    },
		    ended () {
		    	this.$refs.player.currentTime = 0
		    	this.isPlaying = false
		    	this.$refs.player.pause()
		    },
		    get_duration () {
		    	let uri = '/api/open/voice_duration?url='+this.src+'?avinfo'
				this.$http.get(uri)
				.then(response=>{					
					this.true_duration = Math.floor(response.body.data.format.duration)
				},response=>{
					console.log(response.body.message||'获取mp3时长错误')
				})				
			}
		},
		computed: {
			intDuration () {
				let result = this.file.duration || 0
				
				result = util.second_to_minute(Math.ceil(result))
				
				return result
			}
		}
	}



